  margin: 0; }

section.pledges {
  padding: 9.75em 0 8.35em; }
  section.pledges h3 {
    margin-top: 0;
    font-size: 3.125em;
    color: #009dd1;
    font-weight: 700; }
  section.pledges .section-header p {
    font-size: 1.125em; }

.pledge-row:last-of-type .pledge {
  border-bottom: none; }
  @media (max-width: 736px) {
    .pledge-row:last-of-type .pledge:not(:last-of-type) {
      border-bottom: 2px solid #fff; } }

.pledge {
  width: calc(50% - 10px);
  float: left;
  padding: 2.5em 2.5em 2.5em 0;
  border-bottom: 2px solid #fff;
  text-align: left; }
  @media (max-width: 736px) {
    .pledge {
      width: 100%; } }
  .pledge:nth-of-type(even) {
    margin-left: 20px; }
    @media (max-width: 736px) {
      .pledge:nth-of-type(even) {
        margin-left: 0; } }
  .pledge p {
    font-size: 1.125em;
    margin: 0; }

.pledge-icon {
  width: 6em;
  color: #009dd1;
  float: left; }
  .pledge-icon:before {
    margin: 0; }

.pledge-text {
  width: calc(100% - 6em);
  float: left; }
