@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello/fontello.eot?63502124');
  src: url('../fonts/fontello/fontello.eot?63502124#iefix') format('embedded-opentype'),
       url('../fonts/fontello/fontello.woff2?63502124') format('woff2'),
       url('../fonts/fontello/fontello.woff?63502124') format('woff'),
       url('../fonts/fontello/fontello.ttf?63502124') format('truetype'),
       url('../fonts/fontello/fontello.svg?63502124#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}

/**
 * TypeKit Font Flash Fix
 */
h1, h2, h3, h4, h5, h6, p, span, small, table, select, input, i, .faux-table div, label, .number, .tagline {
 opacity: 0;
 transition: opacity 0.20s ease-in;
}
.wf-active,
.wf-inactive, .mce-content-body {
  h1, h2, h3, h4, h5, h6, p, span, a, small, table, select, input, i, .faux-table div, label, .number, .tagline { 
    opacity:1; 
  }
}





